import React, { Component } from 'react';
import { string, shape } from 'react-proptypes';

import Icon from '../../../components/Icon';
import { Logo } from '../../../components/Logo';
import { SignupForm } from '../../../components/SignupForm';

import './MainBlock.scss';

export default class MainBlock extends Component {
    constructor(props) {
        super(props);

        this.state = {
            mode: 'relaxing'
        }
    }

    render() {
        const { params } = this.props;
        const { mode } = this.state;

        return (
            <div className="main-block__container">

                <div className="icon__container">
                    <div className="logo animated">
                        <Logo
                            id="logo-top"
                            className="sh-logo"
                            onLogoReady={this.startChat}
                            mode={mode} />
                    </div>
                    
                    <div className="message__container copytext">
                        {params.message}
                    </div>
                </div>

                <h1 dangerouslySetInnerHTML={{ __html: params.title }} />

                <p className="main-block__description subline2"
                    dangerouslySetInnerHTML={{ __html: params.description }} />

                <SignupForm hasAutofocus={true} />

                <p className="small-label main-block__terms"
                    dangerouslySetInnerHTML={{ __html: params.terms }} />
            </div>
        );
    }
}

MainBlock.propTypes = {
    params: shape({
        title: string,
        description: string,
        inputPlaceholder: string,
        buttonText: string
    })
};



