import React from 'react';

import { Features } from './Features';
import { MainBlock } from './MainBlock';
import { ExtraBlock } from './ExtraBlock';
import { Steps } from './Steps';

export default ({ data }) => {
    const page = data.markdownRemark;
    const { title, features, mainBlock, extraBlock, steps } = page.frontmatter;

    return (
        <div>
            <MainBlock params={mainBlock} />

            <Features params={features}/>

            <Steps params={steps} />

            <ExtraBlock params={extraBlock} />

            <div dangerouslySetInnerHTML={{ __html: page.html }} />
        </div>
    );
};

export const query = graphql`
    query HomePageQuery {
        markdownRemark(fields: { slug: { eq: "/" } }) {
            html
            frontmatter {
                title
                mainBlock {
                    title
                    description
                    terms
                    buttonText
                    inputPlaceholder
                    message
                }
                extraBlock {
                    title
                    description
                }
                features {
                    title
                    items {
                        title
                        icon
                        color
                        text
                    }
                }
                steps {
                    title
                    successText
                    description
                    items {
                        index
                        text
                    }
                }
            }
        }
    }
`;



