import React, { Component } from 'react';

import './Logo.scss';

export default class Logo extends Component {
    constructor(props) {
        super(props);

        this.logo = null;
        this.state = {
            mode: props.mode
        };
        this.loadLogo = this.loadLogo.bind(this);
    }

    componentWillReceiveProps(newProps) {
        if (newProps.mode !== this.state.mode) {
            this.setState({
                mode: newProps.mode
            }, () => {
                try {
                    this.loadAnimation();
                } catch(exp) {}
            });
        }
    }

    componentDidMount() {
        window.addEventListener('load', this.loadLogo);
    }

    componentWillUnmount() {
        window.removeEventListener('load', this.loadLogo);
    }

    loadLogo() {
        this.logo = new window.SHLogo.Logo(this.getId(), () => {
            this.props.onLogoReady && this.props.onLogoReady();
            this.loadAnimation();
        });
    }

    getId() {
        const { id } = this.props;

        return `${id}-sh-logo`;
    }

    loadAnimation() {
        const { mode } = this.state;

        switch (mode) {
            case 'relaxing':
                return this.logo.relaxing();
            case 'waiting':
                return this.logo.waiting();
            case 'working':
            default:
                return this.logo.working();
        }
    }

    render() {
        const { className } = this.props;

        console.log(className);

        return (
            <div id={ this.getId() } className={ className } />
        );
    }
}



