import React from 'react';
import { string, node, func } from 'react-proptypes';

import './Input.scss';

const Input = ({ placeholder, type, name, required, autoFocus }) => {
    return (
        <input className="sh-input__container copytext" 
            placeholder={placeholder} 
            name={name} 
            type={type} 
            required={required} 
            autoFocus={autoFocus} />
    );
}

Input.propTypes = {
    placeholder: string,
    type: string,
    name: string.isRequired
};

Input.defaultProps = {
    placeholder: '',
    type: string,
};

export default Input;



