import React from 'react';
import { string } from 'prop-types';

const renderIcon = (image) => {
  if (image) {
    try {
      const src = require(`./${image}.svg`); // eslint-disable-line
      return (
        <img src={src} alt={image} />
      );
    } catch (e) {
      console.log(e);
    }
  }
  return null;
};

const Icon = ({ image }) => (
  image ?
    <div>
      {renderIcon(image)}
    </div>
  :
    null
);

Icon.propTypes = {
  image: string,
};

Icon.defaultProps = {
  image: '',
};

export default Icon;



