import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';

import './Steps.scss';

const Step = ({ step }) => (
    <div className="step__container">
        <h4 className="step__index">
            {step.index}
        </h4>

        <div className="step__text subline2">
            {step.text}
        </div>
    </div>
);

const Steps = ({ params }) => (
    <div className="steps__container">
        <div className="steps__description">
            <p className="subline1">
                {params.title}
            </p>

            <p className="copytext-large">
                {params.description}
            </p>
        </div>

        <div className="steps__content">
            <div className="steps__list">
                {params.items.map((item, index) => (
                    <Step step={item} key={index} />
                ))}
            </div>

            <h3 className="steps__success">
                {params.successText}
            </h3>

            <div className="steps__background">
            </div>

        </div>        
    </div>
);

Steps.propTypes = {
    title: string,
    description: string,
    successText: string,
    items: arrayOf(shape({
       title: string,
       icon: string,
       color: string,
       text: string 
    }))
};

Steps.defaultProps = {
    items: []
};

export default Steps;



