import React from 'react';
import { string, shape, bool } from 'react-proptypes';

import { Button, Input as ShInput } from '../../ui';

import './SignupForm.scss';

class SignupForm extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            ls: '',
        };
    }

    componentDidMount() {
        const url = new URL(window.location.href);
        const ls = url.searchParams.get('ls') || '';
        this.setState({ ls });
    }

    render() {
        const { buttonText, inputPlaceholder, hasAutofocus } = this.props;

        return (
            <form   className="signup-form__container"
                    action={process.env.API_URL}
                    method="post">
    
                <ShInput placeholder={inputPlaceholder} 
                    name="email"
                    type="email"
                    required
                    autoFocus={hasAutofocus} />

                <input
                    name="leadSource" 
                    value={this.state.ls} 
                    hidden />
        
                <Button type="submit">
                    {buttonText}
                </Button>
            </form>
        );
    }
};

SignupForm.propTypes = {
    buttonText: string,
    inputPlaceholder: string,
    hasAutofocus: bool,
};

SignupForm.defaultProps = {
    buttonText: 'Free Trial',
    inputPlaceholder: 'Input your Work Gmail',
    hasAutofocus: false
};

export default SignupForm;



