import React from 'react';
import { string, shape } from 'react-proptypes';

import { SignupForm } from '../../../components/SignupForm';

import './ExtraBlock.scss';

const ExtraBlock = ({ params }) => (
    <div className="extra-block__container">
        <div className="extra-block__content">

            <h2>
                {params.title}
            </h2>

            <p className="subline2">
                {params.description}
            </p>

            <SignupForm />

        </div>
    </div>
);

ExtraBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       action: string,
    })
};

export default ExtraBlock;



