import React from 'react';
import { string, node, func, bool } from 'react-proptypes';
import cx from 'classnames';

import './Button.scss';

const Button = ({ children, outline, small, onClick }) => {
    const cl = cx({
        'sh-button__container': true,
        'button-text-large': !small,
        'button-text-small': small,
        'sh-button__container--outline': outline,
        'sh-button__container--small': small,
    });

    return (
        <button className={cl}
            onClick={onClick}>
            {children}
        </button>
    );
}

Button.propTypes = {
    children: node,
    type: string,
    outline: bool,
    small: bool,
    onClick: func,
};

Button.defaultProps = {
    children: null,
    type: 'button',
    outline: false,
    small: false,
    onClick: null,
};

export default Button;



